/**
 * Achievement Notification System
 * 
 * Displays achievement unlock notifications with modal dialogs.
 */

import type { Achievement } from '../types/goals.js';
import { showModal } from './modal.js';
import { showNotification } from './notifications.js';

/**
 * Show achievement unlock modal for a single achievement
 */
export function showAchievementModal(achievement: Achievement): void {
    const content = document.createElement('div');
    content.style.textAlign = 'center';
    content.style.padding = '20px';

    content.innerHTML = `
        <div style="font-size: 5em; margin-bottom: 16px; animation: bounce 0.6s ease;">${achievement.icon || 'üèÖ'}</div>
        <h3 style="margin: 0 0 12px 0; color: var(--color-text-primary); font-size: 1.5em;">Achievement Unlocked!</h3>
        <h4 style="margin: 0 0 8px 0; color: var(--color-accent); font-size: 1.2em;">${achievement.title}</h4>
        <p style="margin: 0; color: var(--color-text-secondary); font-size: 1em;">${achievement.description}</p>
    `;

    // Add bounce animation if not already present
    if (!document.getElementById('achievement-animation-style')) {
        const style = document.createElement('style');
        style.id = 'achievement-animation-style';
        style.textContent = `
            @keyframes bounce {
                0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
                40% { transform: translateY(-20px); }
                60% { transform: translateY(-10px); }
            }
        `;
        document.head.appendChild(style);
    }

    showModal({
        title: '',
        content,
        buttons: [
            { text: 'Awesome!', variant: 'primary', onClick: () => { } }
        ],
    });
}

/**
 * Show multiple achievements in a summary view
 */
export function showMultipleAchievements(achievements: Achievement[]): void {
    if (achievements.length === 0) return;

    if (achievements.length === 1) {
        showAchievementModal(achievements[0]);
        return;
    }

    const content = document.createElement('div');
    content.style.padding = '20px';
    content.style.maxHeight = '60vh';
    content.style.overflowY = 'auto';

    content.innerHTML = `
        <div style="text-align: center; margin-bottom: 20px;">
            <div style="font-size: 3em; margin-bottom: 12px;">üéâ</div>
            <h3 style="margin: 0; color: var(--color-text-primary);">Multiple Achievements Unlocked!</h3>
            <p style="margin: 8px 0 0 0; color: var(--color-text-secondary);">You unlocked ${achievements.length} achievements!</p>
        </div>
        <div style="display: grid; gap: 12px;">
            ${achievements.map(a => `
                <div style="
                    background: var(--card-bg);
                    padding: 12px;
                    border-radius: 8px;
                    border: 1px solid var(--color-border);
                    display: flex;
                    gap: 12px;
                    align-items: center;
                ">
                    <div style="font-size: 2.5em;">${a.icon || 'üèÖ'}</div>
                    <div>
                        <h4 style="margin: 0 0 4px 0; color: var(--color-accent);">${a.title}</h4>
                        <p style="margin: 0; font-size: 0.9em; color: var(--color-text-secondary);">${a.description}</p>
                    </div>
                </div>
            `).join('')}
        </div>
    `;

    showModal({
        title: '',
        content,
        buttons: [
            { text: 'Great!', variant: 'primary', onClick: () => { } }
        ],
    });
}

/**
 * Show toast notification for achievements (less intrusive)
 */
export function showAchievementToast(achievement: Achievement): void {
    showNotification(`üèÜ ${achievement.title}: ${achievement.description}`, 'success');
}

/**
 * Check for achievements and show notifications
 * Call this after workout save or other relevant actions
 */
export async function checkAndNotifyAchievements(): Promise<void> {
    try {
        const { goalsStore } = await import('../state/GoalsStore.js');
        const newAchievements = await goalsStore.checkAndUnlockAchievements();

        if (newAchievements.length > 0) {
            // Show modal for new achievements
            showMultipleAchievements(newAchievements);
        }
    } catch (error) {
        console.error('Error checking achievements:', error);
    }
}
